
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    text-align: center;
    margin-top: 60px;
}
.block,
.viewimg {
    margin: 15px;
}
.btn {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
}
.viewimg {
    max-width: 200px;
    border: 1px dashed #ccc;
    border-radius: 4px;
    padding: 5px;
    background-color: #ccc;
}



